/* ------------------------------------------------------------ *\
    Screen-two
\* ------------------------------------------------------------ */

.screen-two {
    background: url("/css/images/background.png") repeat;
    & .main-content {
        padding: 30px 0;
        & .main-content-inner {
            width: 90%;
            margin: 0 auto;
        }
        @media #{$desktop} {
            .main-content-inner {
                width: 980px;
            }
        }
        & h1 {
            font-size: 20px;
            font-weight: $font-weight-2;
            font-family: $font-family-1;
            color: $red;
            text-align: center;
            padding-bottom: 20px;
            border-bottom: 4px dotted $red;
        }
        @media #{$tablet} {
            h1 {
                font-size: $font-size-30;
            }
        }
        & .text {
            padding: 40px 0;
            color: $black;
            font-size: $font-size-13;
            line-height: 30px;
            word-wrap: break-word;
            word-break: break-all;
        }
        @media #{$tablet} {
            .text {
                font-size: 16px;
            }
        }
        @media #{$desktop} {
            .text {
                padding: 200px 0;
            }
        }
        & p {
            padding: 10px 0;
        }
    }
}