/* ------------------------------------------------------------ *\
    Screen-four
\* ------------------------------------------------------------ */

.screen-four {
    background: url("/css/images/background.png") repeat;
    border-bottom: 4px dotted $red;
    & .main-content {
        padding: 30px 0;
        & .main-content-inner {
            width: 90%;
            margin: 0 auto;
            & h1 {
                font-size: 20px;
                font-weight: $font-weight-2;
                font-family: $font-family-1;
                color: $red;
                text-align: center;
                padding-bottom: 20px;
                border-bottom: 4px dotted $red;
            }
            @media #{$tablet} {
                h1 {
                    font-size: $font-size-30;
                }
            }
            & table {
                border-collapse: collapse;
                width: 100%;
                color: $gray;
                font-size: 10px;
            }
            @media #{$tablet} {
                table {
                    font-size: $font-size-13;
                }
            }
            & thead {
                display: none;
                & th {
                    padding: 5px 0;
                }
            }
            & tr {
                border: 1px solid $red;
                display: block;
                margin-bottom: 10px;
                & td {
                    padding: 5px 0;
                    display: block;
                    text-align: right;
                    font-weight: $font-weight-2;
                    padding: 5px 10px;
                    &.title {
                        font-weight: $font-weight-2;
                        background: #f8e6e8;
                        text-align: center;
                        color: $black;
                    }
                    &::before {
                        content: attr(data-label);
                        float: left;
                        font-weight: $font-weight-2;
                        color: $black;
                        text-transform: uppercase;
                    }
                }
            }
        }
    }
}

@media #{$desktop} {
    .screen-four {
        border-bottom: none;
        & .main-content {
            padding: 200px 0;
            & .main-content-inner {
                width: 980px;
                margin: 0 auto;
                & table {
                    border: 1px solid $red;
                    font-size: 16px;
                    & thead {
                        display: table-header-group;
                        color: $white;
                        font-size: 18px;
                        & th {
                            text-align: left;
                            background: $red;
                        }
                    }
                    & tr {
                        display: table-row;
                        border: none;
                        & td {
                            display: table-cell;
                            text-align: left;
                            padding-right: 0;
                            padding-left: 0;
                            color: #404040;
                            font-weight: none;
                            &.title {
                                background: none;
                                text-align: left;
                                text-indent: 10px;
                                color: #404040;
                                font-weight: none;
                            }
                            &::before {
                                display: none;
                            }
                            &:hover {
                                color: $black;
                            }
                        }
                        &:nth-child(2n+1) {
                            background: #f8e6e8;
                        }
                    }
                }
            }
        }
    }
}