/* ------------------------------------------------------------ *\
    Screen-three
\* ------------------------------------------------------------ */

.screen-three {
    background-color: $red;
    & .upper-main-content {
        background: url("/css/images/background.png") repeat;
        padding-top: 30px;
        & .main-content-inner {
            width: 90%;
            margin: 0 auto;
        }
        @media #{$desktop} {
            .main-content-inner {
                width: 980px;
            }
        }
    }
    & h1 {
        font-size: 20px;
        font-weight: $font-weight-2;
        font-family: $font-family-1;
        color: $red;
        text-align: center;
        padding-bottom: 20px;
        border-bottom: 4px dotted $red;
    }
    @media #{$tablet} {
        h1 {
            font-size: $font-size-30;
        }
    }
    & .lower-main-content {
        & .main-content-inner {
            width: 90%;
            margin: 0 auto;
        }
        @media #{$desktop} {
            .main-content-inner {
                width: 980px;
            }
        }
        & .content {
            color: $white;
            font-size: $font-size-13;
            line-height: 26px;
            text-align: center;
            & div {
                padding: 20px 0;
                & h2 {
                    color: $white;
                    font-size: 16px;
                    text-align: center;
                    padding: 20px 0;
                }
                @media #{$tablet} {
                    h2 {
                        font-size: $font-size-26;
                    }
                }
            }
        }
        @media #{$tablet} {
            .content {
                padding: 100px 0;
                & div {
                    width: 50%;
                    display: inline-block;
                    & p {
                        width: 70%;
                        margin: 0 auto;
                    }
                    &:nth-child(even) {
                        float: right;
                        overflow: hidden;
                        &:after {
                            clear: both;
                            display: block;
                            content: "";
                        }
                    }
                }
            }
        }
        @media #{$desktop} {
            & .content {
                padding: 200px 0;
                & div {
                    width: 24%;
                    padding: 0;
                    float: left;
                    overflow: hidden;
                    &:nth-child(even) {
                        float: left;
                        overflow: hidden;
                    }
                }
            }
        }
    }
}