/* ------------------------------------------------------------ *\
    Screen-five
\* ------------------------------------------------------------ */

.screen-five {
    color: $black;
    & .main-content {
        padding: 30px 0;
    }
    @media #{$desktop} {
        .main-content {
            padding: 200px 0;
        }
    }
    & .main-content-inner {
        width: 90%;
        margin: 0 auto;
        & .item-center {
            width: 100%;
            margin: 0 auto;
            & .accordion {
                width: 300px;
                margin: 0 auto;
                & h3 {
                    font-size: 16;
                    font-weight: $font-weight-2;
                    padding: 20px 0;
                    color: $red;
                    text-align: center;
                }
            }
            & .accordion-section-title {
                cursor: pointer;
                display: block;
                font-weight: $font-weight-2;
                font-size: $font-size-13;
                padding: 10px 0;
                word-break: break-all;
                &::before {
                    content: url("/css/images/arrow-down.png");
                    display: inline;
                    vertical-align: -webkit-baseline-middle;
                    margin-right: 10px;
                }
            }
            & .accordion-section-content {
                display: none;
                font-size: $font-size-13;
                word-break: break-all;
                color: $dark;
                width: 100%;
            }
            & .active {
                color: $red;
            }
            & .accordion-section-title.active::before {
                content: url("/css/images/arrow-up.png");
                display: inline;
                vertical-align: -webkit-baseline-middle;
                margin-right: 10px;
            }
        }
    }
    @media #{$desktop} {
        & .main-content-inner {
            width: 980px;
            margin: 0 auto;
            & .item-center .accordion {
                width: 33%;
                float: left;
                padding: 0 15px;
                & h3 {
                    font-size: $font-size-23;
                }
                &:last-child {
                    float: right;
                }
                & ::after {
                    clear: both;
                    display: block;
                    content: "";
                }
            }
        }
    }
}